import React from 'react';
import {Route, Routes} from "react-router";
import SimpleHeader from "../../components/header/simple-header.component";
import SimpleFooter from "../../components/footer/simple-footer.component";
import SignInAndSignUpPage from "../sign-in-and-sign-up/sign-in-and-sign-up.component";
import OTPage from "./enroll/otp/one-time-password.component";
import PhonePage from "./enroll/phone-number/phone.component";
import PasswordPage from "./enroll/password/password.component";

const LoginPage = () => {
    return (
        <>
            <SimpleHeader/>
            <Routes>
                <Route path="/" element={<SignInAndSignUpPage/>}/>
                <Route path="/phone" element={<PhonePage/>}/>
                <Route path="/otp" element={<OTPage/>}/>
                <Route path="/otp/password" element={<PasswordPage/>}/>
                <Route path="/*" element={<div/>}/>

            </Routes>
            <SimpleFooter/>
        </>

    )
}

export default LoginPage;
