import React, {useState} from "react";
import StepsBar from "../../../../components/steps-bar/steps-bar.component";
import {BoldTitle, InputLabel} from "../../../../global.styles";

import flag_icon from 'assets/images/icono-bandera.svg';
import person_image from 'assets/images/ilustracion-persona-p1.svg';
import {
    ButtonContainer,
    FlagIcon,
    Line,
    NumberStyle,
    PersonImg,
    PhoneBox,
    PhoneContainer,
    SmallLabel
} from "./phone.styles";
import FormInput from "../../../../components/form-input/form-input.component";
import {ButtonsBarContainer} from "../../../../components/sign-in/sign-in.styles";
import CustomButton from "../../../../components/custom-button/custom-button.component";
import Input from "../../../../components/form-input/input.component";

const PhonePage = () => {
    const [phone, setPhone] = useState('');


    const handleSubmit = async event => {
        event.preventDefault();

    };

    const handleChange = event => {

        setPhone(event.target.value);
    };
    return (
        <PhoneContainer>
            <div>
                <StepsBar/>
                <BoldTitle>Verifica que eres tu</BoldTitle>
                <InputLabel>Numero de celular</InputLabel>
                <form onSubmit={handleSubmit}>
                    <PhoneBox>

                        <FlagIcon>
                            <img src={flag_icon}
                                 className="flag_icon"/>
                        </FlagIcon>
                        <Line/>

                        <NumberStyle>+52</NumberStyle>

                        <div>
                            <Input
                                name='phone'
                                type='tel'
                                handleChange={handleChange}
                                value={phone}
                                autoComplete='off'
                                required
                            />
                        </div>


                    </PhoneBox>
                    <SmallLabel>Son 10 numeros</SmallLabel>
                    <ButtonContainer>
                        <CustomButton type='submit'> Continuar </CustomButton>
                    </ButtonContainer>

                </form>

            </div>

            <PersonImg>
                <img src={person_image}
                     className="person"/>
            </PersonImg>

        </PhoneContainer>
    );
}

export default PhonePage;