import styled, {createGlobalStyle} from 'styled-components';

export const GlobalStyle = createGlobalStyle`
  body {
    padding: 0px;
    margin: 0px;

    @media screen and (max-width: 800px) {
     // padding: 10px;
    }
  }

  a {
    text-decoration: none;
    color: black;
  }

  * {
    @import url('../fonts/');
    font-family: Poppins !important;
    box-sizing: border-box;
  }

  .routes-container {
    padding: 20px 70px;
    background-color: #f5f5f5;
  }
`;

export const BoldTitle = styled.span`
  //width: 464px;
  //height: 45px;
  //margin: 16px 0 0;
  padding: 16px 0 0;  
  font-family: Poppins;
  font-size: 32px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #222e41;
`;

export const StepLabel = styled.span`
  //width: 99px;
  //height: 25px;
  //margin: 0 365px 8px 0;
  padding: 8px 16px;
  font-family: Poppins;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #747f92;
`;

export const InputLabel = styled.span`
  //width: 343px;
  //height: 25px;
  //margin: 0 121px 8px 0;
  padding: 16px 0 0;
  display: block;
  font-family: Poppins;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #222e41;
`;

export const Bar = styled.div`
  //padding: 8px 16px;
  width: 464px;
  height: 8px;
  margin: 8px 0 16px;
  padding: 0 309px 0 0;
  border-radius: 6px;
  background-color: #f5f5f5;
`;

export const Rectangle = styled.div`
  width: 155px;
  height: 8px;
  border-radius: 4px;
  background-color: #5dc137;
`;