import React from 'react'
import {BoxContent, FooterContainer, Line, LogoContainer, OptionsContainer} from "./footer.styles";
import {ReactComponent as Logo} from "../../assets/images/logo-desktop.svg";
import SocialMedia from "./social/social.component";
import Contact from "./contact/contact.component";
import Links from "./links/links-container.component";
import Copy from "./copy/copy.component";
import Stores from "./stores/stores.component";


const Footer = () => (
    <FooterContainer>
        <BoxContent>
            <OptionsContainer>
                <div className="box-1">
                    <LogoContainer to='/'>
                        <Logo className='logo'/>
                    </LogoContainer>
                    <SocialMedia/>
                </div>
                <div className="box-2">
                    <Contact/>
                    <Links/>
                    <Stores/>
                </div>
            </OptionsContainer>
        </BoxContent>
        <Line/>
        <Copy/>
    </FooterContainer>
);

export default Footer;