import {all, call, put, takeLatest} from "redux-saga/effects";

import UserActionTypes from "./user.types";
import {
    signInFailure,
    signInSuccess,
    signOutSuccess,
    signOutFailure,
    signUpSuccess,
    signUpFailure
} from "./user.actions";

//import {auth, createUserProfileDocument, googleProvider, getCurrentUser} from "../../firebase/firebase.utils";

