import React from 'react';
import {connect} from 'react-redux';
import {createStructuredSelector} from 'reselect';


import {selectCurrentUser} from '../../redux/user/user.selectors';
import {signOutStart} from '../../redux/user/user.actions';

import {ReactComponent as Logo} from '../../assets/baz.svg';
import {ReactComponent as Avatar} from '../../assets/avatar-2.svg';
import {ReactComponent as BellNotifications} from '../../assets/icons-notification-off.svg';

import {
    HeaderContainer,
    LogoContainer,
    OptionsContainer,
    OptionLink, AvatarContainer, NotificationContainer
} from './header.styles';

const Header = ({currentUser, hidden, signOutStart}) => (
    <HeaderContainer>
        <LogoContainer to='/'>
            <Logo className='logo'/>
        </LogoContainer>
        <OptionsContainer>


            {currentUser ? (
                <>
                    <OptionLink to='/movies/resume'>RESUME</OptionLink>
                    <OptionLink to='/movies'>MOVIES</OptionLink>
                    <OptionLink as='div' onClick={signOutStart}>
                        SIGN OUT
                    </OptionLink>
                    <AvatarContainer to='/'>
                        <Avatar className='avatar'/>
                    </AvatarContainer>
                    <NotificationContainer to='/'>
                        <BellNotifications className='bell'/>
                    </NotificationContainer>
                </>
            ) : (
                <OptionLink to='/signin'>SIGN IN</OptionLink>
            )}

        </OptionsContainer>


    </HeaderContainer>
);

const mapStateToProps = createStructuredSelector({
    currentUser: selectCurrentUser
});

const mapDispatchToProps = dispatch => ({
    signOutStart: () => dispatch(signOutStart())
});

export default connect(mapStateToProps, mapDispatchToProps)(Header);
