import styled, {css} from 'styled-components';

const subColor = 'grey';
const mainColor = '#222e41';

const shrinkLabelStyles = css`
  top: -14px;
  font-size: 18px;
  color: ${mainColor};
`;

export const GroupContainer = styled.div`
  position: relative;
  margin: 45px 0;

  input[type='password'] {
    letter-spacing: 0.3em;
  }

  input[type='tel'] {
    letter-spacing: 0.3em;
  }
`;

export const FormInputContainer = styled.input`

  //width: 324px;
  //height: 22px;
  display: block;
  height: 48px;
  width: 100%;
  font-family: Poppins;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #222e41;
  border-radius: 8px;
  border: solid 1px #c2c2c2;
  background-color: #fff;

  &:focus {
    outline: none;
  }

  &:focus ~ label {
    ${shrinkLabelStyles}
  }
`;

export const FormInputLabel = styled.label`
  color: ${subColor};

  //width: 47px;
  //height: 25px;
  //margin: 0 417px 8px 0;
  font-family: Poppins;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;

  //font-size: 16px;
  //font-weight: normal;
  position: absolute;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 300ms ease all;

  &.shrink {
    ${shrinkLabelStyles}
  }
`;