import React, {Suspense, useEffect} from 'react';
import Spinner from "./components/spinner/spinner.component";
import ErrorBoundary from "./components/error-boundary/error-boundary.component";

import {Navigate, Route, Routes} from "react-router-dom";
import {connect} from "react-redux";
import {createStructuredSelector} from "reselect";
import {selectCurrentUser} from "./redux/user/user.selectors";
import {checkUserSession} from "./redux/user/user.actions";

import {GlobalStyle} from "./global.styles";
import HomePage from "./pages/homepage/homepage.component";
import {PublicRoutes} from "./routes/public-routes.component";
import {PrivateRoutes} from "./routes/private-routes.component";
import LoginPage from "./pages/login/login.component";
import SignInAndSignUpPage from "./pages/sign-in-and-sign-up/sign-in-and-sign-up.component";
import Footer from "./components/footer/footer.component";
import { ResumeMovie } from './pages/movies/resume/resume.movie.component';


const BazWebApp = ({checkUserSession, currentUser}) => {

    useEffect(() => {
        checkUserSession()
    }, [checkUserSession]);

    return (
        <>
            <GlobalStyle/>
            {/*<Header/>*/}
            <ErrorBoundary>
                <Suspense fallback={<Spinner/>}>
                    {/*<div className='routes-container'>*/}

                    <Routes>
                        <Route
                            path="/enroll/*"
                            element={
                                <PublicRoutes currentUser={currentUser}>
                                    <LoginPage/>
                                </PublicRoutes>
                            }
                        />

                        <Route
                            path="/*"
                            element={
                                <PrivateRoutes currentUser={currentUser}>
                                    <HomePage/>
                                </PrivateRoutes>
                            }
                        />

                    </Routes>


                    {/*        <Routes>
                            <Route path='/' element={<HomePage/>}/>

                            <Route path='/signin'
                                   element={currentUser ? (<Navigate to="/"/>) : (<SignInAndSignUpPage/>)}/>
                            <Route path='/shop/*'
                                   element={<ShopPage/>}/>
                            <Route path='/resume/*'
                                   element={<ResumeMovie/>}/>       

                        </Routes>*/}
                    {/*</div>*/}
                </Suspense>
            </ErrorBoundary>
            {/*<Footer/>*/}
        </>

    );

}

const mapStateToProps = createStructuredSelector({
    currentUser: selectCurrentUser
});

const mapDispatchToProps = dispatch => ({
    checkUserSession: () => dispatch(checkUserSession())
});

export default connect(mapStateToProps, mapDispatchToProps)(BazWebApp);
