import React from 'react'
import {BoxContent, FooterContainer, Line, LogoContainer, OptionsContainer} from "./footer.styles";
import {ReactComponent as Logo} from "../../assets/images/logo-desktop.svg";
import Links from "./links/links-container.component";
import Copy from "./copy/copy.component";

const SimpleFooter = () => {
    return (
        <FooterContainer>
            <BoxContent>
                <OptionsContainer>
                    <div className="box-1">
                        <LogoContainer to='/'>
                            <Logo className='logo'/>
                        </LogoContainer>
                        <Links/>
                    </div>
                </OptionsContainer>
            </BoxContent>
            <Line/>
            <Copy/>
        </FooterContainer>
    )
}

export default SimpleFooter;
