import styled from 'styled-components';

export const PlayerContainer = styled.div`
  width: 100%;
  height:100%;
  position: relative;

  button{
    color: #ffff;
    font-size: 20px;;
    background: none;
    border: none;
    transform: scale(0.9);
    &:hover {
      color: #fff;
      transform: scale(1);
      cursor: pointer;
    }
  }
`;

export const Controls = styled.div`
  color: #ffff;;
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  background: rgb(0,0,0,0.6);
  border: 1px solid black;
  display: flex;
  flex-direction: column;
`;

export const TopControls = styled.div`
  //position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  direction: row;
  justify-content: space-between;

  button img {
    width: 18px;
  }
`;
export const MiddleControls = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  direction: row;
  align-items: center;
`;
export const BottomControls = styled.div`
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  flex-direction:column;
  //justify-content: space-between;
`;

export const Seekbar = styled.div`
  display: flex;
  flex-direction:column;
  //justify-content: space-between; 
`;

export const Buttons = styled.div`
  display: flex;
  flex-direction:row;
  justify-content: space-between;
  max-height: 50px;

  .align-center{
    font-stretch: expanded;
    padding-top: 10px;

  }

  .align-right {
    display: flex;
    flex-direction: row;
    input{
      width:100%;
      max-width:60px;
    }

  }
`;
