import React, {lazy, Suspense, useEffect} from 'react';
import {Route, Routes, useParams} from 'react-router-dom';
import {connect} from 'react-redux';

import {fetchCollectionsStart} from '../../redux/shop/shop.actions';
import Spinner from "../../components/spinner/spinner.component";
import SearchBox from "../../components/search-box/search-box.component";
import {MoviesPageContainer} from "./movies.styles";
import {ResumeMovie} from "./resume/resume.movie.component";
//import CollectionsOverviewContainer from '../../components/collections-overview/collections-overview.container';
//import CollectionPageContainer from '../collection/collection.container';

const CollectionsOverviewContainer = lazy(() => import('../../components/collections-overview/collections-overview.container'));
const CollectionPageContainer = lazy(() => import('../collection/collection.container'))

const MoviesPage = ({fetchCollectionsStart, match}) => {
    let params = useParams();
    console.log(params)
    useEffect(() => {
        fetchCollectionsStart();
    }, [fetchCollectionsStart]);

    return (
        <MoviesPageContainer>
            <Suspense fallback={<Spinner/>}>
                <SearchBox/>
                <div className='movies'>Peliculas</div>
                <Routes>
                    <Route index path="/"
                           element={<CollectionsOverviewContainer/>}
                    />
                    <Route path="/:collectionId"
                           element={<CollectionPageContainer params={params}/>}
                    />

                    <Route path="/resume"
                           element={<ResumeMovie params={params}/>}
                    />

                </Routes>
            </Suspense>
        </MoviesPageContainer>
    );
};

const mapDispatchToProps = dispatch => ({
    fetchCollectionsStart: () => dispatch(fetchCollectionsStart())
});

export default connect(null, mapDispatchToProps)(MoviesPage);