import React from 'react';
import {Route, Routes} from "react-router";

import {HomePageContainer} from './homepage.styles';
import Header from "../../components/header/header.component";
import Footer from "../../components/footer/footer.component";
import ShopPage from "../shop/shop.component";
import {HomeScreen} from "../../componentsBackup/home/HomeScreen";
import DashBoard from "../../components/dashboard/dashboard.component";
import SimpleFooter from "../../components/footer/simple-footer.component";
import SimpleHeader from "../../components/header/simple-header.component";
import MoviesPage from "../movies/movies.component";

const HomePage = () => {
    return (
        <>
            <Header/>
            <HomePageContainer>
                <Routes>
                    <Route path="/" element={<DashBoard/>}/>
                    <Route path='/movies/*'
                           element={<MoviesPage/>}/>

                    {/*<Route path="/" element={<HomeScreen/>}/>
                    <Route path="/movies" element={<MoviesScreen/>}/>
                    <Route path="/detailMovie/:movieId" element={<MovieSynopsis/>}/>
                    <Route path="/categories/:categoryId" element={<CategoryMovieScreen/>}/>
                    <Route path="/rental/:misRentas" element={<MisRentasScreen/>}/>
                    <Route path="/*" element={<NotFound/>}/>*/}
                </Routes>
            </HomePageContainer>
            <Footer/>
        </>
    );
}

export default HomePage;