import React,  { useState, useRef, useEffect } from "react";
import ReactPlayer from 'react-player'
import screenful from 'screenfull'
import {
    PlayerContainer,
    Controls,
    TopControls,
    MiddleControls,
    BottomControls,
    Seekbar,
    Buttons
  } from './player.styles'

import ForwardTenIcon from '../../assets/images/icons-audiovisual-fast-forward-10-sec-off.png'
import RewindTenIcon from '../../assets/images/icons-audiovisual-rewind-10-sec-off.png'
import MaximizeIcon from '../../assets/images/maximize-2.png'
import VolumeIcon from '../../assets/images/volume-2.png'
import playIcon from '../../assets/images/iconos-sistema-accion-sds-icons-reproducir.png'
import PauseIcon from '../../assets/images/icons-audiovisual-pause-02-off.png'
import buttonGoBack from '../../assets/images/icons-system-navigation-back-off.png'

export const Player = ({stream}) => {

    //const url ='https://www.learningcontainer.com/wp-content/uploads/2020/05/sample-mp4-file.mp4'
    const url ='https://bitdash-a.akamaihd.net/content/MI201109210084_1/m3u8s/f08e80da-bf1d-4e3d-8899-f0f6155f6efa.m3u8'
    const [showControls, setShowControls] = useState(false);
    // const [count, setCount] = useState(0);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [timeDisplayFormat, setTimeDisplayFormat] = React.useState("normal");
    const [bookmarks, setBookmarks] = useState([]);
    const [state, setState] = useState({
      pip: false,
      playing: false,
      controls: false,
      light: false,
      muted: false,
      played: 0,
      duration: 0,
      playbackRate: 1.0,
      volume: 1,
      loop: false,
      seeking: true,
    });
  
    const playerRef = useRef(null);
    const playerContainerRef = useRef(null);
    const controlsRef = useRef(null);
    const canvasRef = useRef(null);
    const {
      playing,
      controls,
      light,
      muted,
      loop,
      playbackRate,
      pip,
      played,
      seeking,
      volume,
    } = state;

    
    const handlePlayPause = () => {
        setState({ ...state, playing: !state.playing });
      };
    
      const handleRewind = () => {
        playerRef.current.seekTo(playerRef.current.getCurrentTime() - 10);
      };
    
      const handleFastForward = () => {
        playerRef.current.seekTo(playerRef.current.getCurrentTime() + 10);
      };
    
      const handleProgress = (changeState) => {
        if (count > 3) {
          controlsRef.current.style.visibility = "hidden";
          count = 0;
        }
        if (controlsRef.current.style.visibility == "visible") {
          count += 1;
        }
        if (!state.seeking) {
          setState({ ...state, ...changeState });
        }
      };
    
      const handleSeekChange = (e, newValue) => {
        console.log({ newValue });
        setState({ ...state, played: parseFloat(newValue / 100) });
      };
    
      const handleSeekMouseDown = (e) => {
        setState({ ...state, seeking: true });
      };
    
      const handleSeekMouseUp = (e, newValue) => {
        console.log({ value: e.target });
        setState({ ...state, seeking: false });
        // console.log(sliderRef.current.value)
        playerRef.current.seekTo(newValue / 100, "fraction");
      };
    
      const handleDuration = (duration) => {
        setState({ ...state, duration });
      };
    
      const handleVolumeSeekDown = (e, newValue) => {
        setState({ ...state, seeking: false, volume: parseFloat(newValue / 100) });
      };
      const handleVolumeChange = (e, newValue) => {
         console.log(newValue);
        setState({
          ...state,
          volume: parseFloat(newValue / 100),
          muted: newValue === 0 ? true : false,
        });
      };
    
      const toggleFullScreen = () => {
        screenful.toggle(playerContainerRef.current);
      };
    
      const handleMouseMove = () => {
        console.log("mousemove");
        controlsRef.current.style.visibility = "visible";
        count = 0;
      };
    
      const hanldeMouseLeave = () => {
        controlsRef.current.style.visibility = "hidden";
        count = 0;
      };
    
      const handleDisplayFormat = () => {
        setTimeDisplayFormat(
          timeDisplayFormat == "normal" ? "remaining" : "normal"
        );
      };
    
      const handlePlaybackRate = (rate) => {
        setState({ ...state, playbackRate: rate });
      };
    
      const hanldeMute = () => {
        setState({ ...state, muted: !state.muted });
      };
    

    return (
        <PlayerContainer
        ref={playerContainerRef}
        >
            <ReactPlayer
                    ref={playerRef}
                    width='100%'
                    height='auto'
                    url={url}
                    pip={pip}
                    playing={playing}
                    controls={false}
                    light={light}
                    loop={loop}
                    playbackRate={playbackRate}
                    volume={volume}
                    muted={muted}
                    
                />
                <Controls>
                    <TopControls><button><img src={buttonGoBack}/>Regresar</button></TopControls>
                    <MiddleControls>MIDDLE CONTROLS</MiddleControls>
                    <BottomControls>
                      <Seekbar>
                      <input type="range" 
                      className="seeking"
                      min="0"
                      max="100"
                      
                      value={played * 100}
                      onChange={handleSeekChange}
                      onMouseDown={handleSeekMouseDown}
                      onChangeCommitted={handleSeekMouseUp}
                      onDuration={handleDuration}
                      />
                      </Seekbar>
                      <Buttons>
                      <div className="align-left">
                        <button onClick={handlePlayPause}><img src={playing ? PauseIcon : playIcon} alt="" /></button>
                        <button><img onClick={handleRewind} src={RewindTenIcon} alt="" /></button>
                        <button><img onClick={handleFastForward} src={ForwardTenIcon} alt="" /></button>
                      </div>
                      <div className="align-center">
                        <span className="movieTitle">Sin tiempo para morir</span>
                      </div>
                      <div className="align-right">
                        <button><img onClick={hanldeMute} src={VolumeIcon} alt="" /></button>
                        <input type="range" className="volume" value={volume} min="0" max="100" step="0.1" />
                        <button onClick={toggleFullScreen}><img src={MaximizeIcon} alt="" /></button>
                      </div>
                      </Buttons>
                    </BottomControls>
                </Controls>
        </PlayerContainer>
    )
}
