import React from "react";
import {LogoContainer} from "../footer.styles";
import baz_qr from "../../../assets/images/qr-superapp-2-2-x.png";
import google_play from "../../../assets/images/google-play.svg";
import apple_store from "../../../assets/images/group-2-copy-2.svg";
import {StoresContainer} from "./stores.styles";

const Stores = () => (
    <StoresContainer>
        <LogoContainer to='/'>
            <img src={baz_qr} className="logo_qr" alt="logo"/>
        </LogoContainer>

        <div className='stores'>
            <label className='item-store'>Escanea el codigo <br/> con tu celular</label>
            <img src={google_play} className='item-store logo_store' alt="logo"/>
            <img src={apple_store} className='item-store logo_store' alt="logo"/>
        </div>
    </StoresContainer>
);

export default Stores;