import React from 'react'
import {HeaderContainer, LogoContainer} from "./header.styles";
import {ReactComponent as Logo} from "../../assets/baz.svg";

const SimpleHeader = () => {
    return (
        <HeaderContainer>
            <LogoContainer to='/'>
                <Logo className='logo'/>
            </LogoContainer>
        </HeaderContainer>
    )
}

export default SimpleHeader;
