import React from 'react'

import CustomButton from './../custom-button/custom-button.component';
import { ButtonsBarContainer } from '../sign-in/sign-in.styles';
import {
    SypnosisContainer,
    ItemTitle,
    ItemSpecs,
    Description,
    DescriptionTitle,
    DescriptionInfo,
    ItemPrice,
    Price
} from './resume.item.style';

export const Synopsis = ({data}) => {
    return (
        <SypnosisContainer>
            <ItemTitle>
                <span>Sin tiempo para morir</span>    
            </ItemTitle>
            <ItemSpecs>
                <ul>
                    <li>HD</li>
                    <li>B15</li>
                    <li>2021</li>
                    <li>1h 32 min</li>
                    <li>Idiomas: Inglés, Español</li>
                </ul>
            </ItemSpecs> 
            
            <Description>
                <DescriptionTitle>Descripción</DescriptionTitle>                      
                <DescriptionInfo>
                    Seis personas se encuentran encerradas de manera inesperada en una nueva serie de escape rooms, revelando paulatinamente aquello que tienen en común para sobrevivir…
                </DescriptionInfo>
            </Description>
            
            <ItemPrice>
                <Price>HD $50.00</Price>
                <span>IVA incluido.</span>
            </ItemPrice>
            <ButtonsBarContainer>
                    <CustomButton type='submit'> Rentar </CustomButton>

                </ButtonsBarContainer>
        </SypnosisContainer>
    )
}
