import React from "react";
import {OptionLink} from "../footer.styles";
import icon_facebook from "../../../assets/images/icono-footer-facebook.svg";
import icon_instagram from "../../../assets/images/icono-footer-instagram.svg";
import icon_twitter from "../../../assets/images/icono-footer-twitter.svg";
import icon_youtube from "../../../assets/images/icono-footer-youtube.svg";
import {SocialBox, SocialContainer} from "./social.styles";

const SocialMedia = () => (
    <SocialContainer>
        <SocialBox>
            <li>
                <OptionLink to='/'> <img src={icon_facebook} alt="logo"/></OptionLink>
            </li>
            <li>
                <OptionLink to='/'> <img src={icon_instagram} alt="logo"/></OptionLink>
            </li>
            <li>
                <OptionLink to='/'> <img src={icon_twitter} alt="logo"/></OptionLink>
            </li>
            <li>
                <OptionLink to='/'> <img src={icon_youtube} alt="logo"/></OptionLink>
            </li>
        </SocialBox>
    </SocialContainer>
);

export default SocialMedia;